import React, { useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom'; 
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper/modules';

import Popular from '../db/popular.json';
import SearchCompo from './SearchCompo';

import "swiper/css";
// import "swiper/css/pagination";
//========== ========== ========== ========== ========== ========== ==========

/* Home전용 인기 책, 블로그리뷰, 유튜브리뷰 json */
const popular_popularBook = Popular.popularBook,
      popular_popularBlogReview = Popular.popularBlogReview,
      popular_popularYoutubeReview = Popular.popularYoutubeReview;

// let swiperStopStart;
//========== ========== ========== ========== ========== ========== ==========           
export default function Home() {

    //navigate Hook얻어서 moveTo 함수에 적용
    const navigate = useNavigate(); 
    const moveTo = (url) => {
        navigate(url);
    }//moveTo(url) 함수정의

    //a태그 이동
    const moveToUrl = (url) => {
        // window.location.href = url; // 이거 쓰면 그 창에서 이동함
        window.open(url, '_blank'); // 새 창 만들어와서 불러오삼
    }//moveToUrl(url) 함수정의

    const changeWord = (oriStr, a, b) => {
        let RE = new RegExp(a, "g"); //"g": global //모든 a 내용 잡음
        return oriStr.replace(RE, b); //모든 a내용을 b로 바꿈
    }//changeWord(oriStr, a, b) 함수정의


    const changeMultiWord = (oriStr, aArr, bArr) => {
        // 원본 문자열을 반복해서 모든 교체 목록을 적용합니다.
        let changeStr = oriStr;//원본문자열 담기

        for (let i = 0; i < aArr.length; i++) {
          let RE = new RegExp(aArr[i], "g");
          changeStr = changeStr.replace(RE, bArr[i]);
        }

        return changeStr;
    }//changeMultiWord(oriStr, aArr, bArr) 함수정의

    const titleFun = (oriTitle='') => {
        let end = oriTitle.indexOf('(');
      
        if(end <= -1){
          return oriTitle;
        }
        return oriTitle.slice(0, end);
    }//titleFun()  함수정의

    // useEffect(()=>{

    //     // const ElpopularBookSwiper = document.querySelector('.popularBookSwiper');
    //     // swiperStopStart = (startBool = ture) => {
    //     //     if(startBool){
    //     //         ElpopularBookSwiper.autoplay.start();
    //     //     }else{
    //     //         ElpopularBookSwiper.autoplay.stop();
    //     //     }
    //     // }

    //     let elYoutubeVideo = useRef();
    // },[])

    // const activeYoutubeFun = (key) => {
    //     console.log('활성화 클릭됨?', elYoutubeVideo);
        
    //     // elYoutubeVideo.setAttribute('src', `https://www.youtube.com/embed/${key}`);
    //     // elYoutubeVideo.style.display = 'block';
    //     // elYoutubeVideo.classList.add(active);
    // }
    
    // const inActiveYoutubeFun = () => {
    //     console.log('비활성화 클릭됨?', elYoutubeVideo);
    //     // elYoutubeVideo.setAttribute('src', '');
    //     // elYoutubeVideo.style.display = 'none';
    //     // elYoutubeVideo.classList.remove(active);
    // }

    
//---------- ---------- ---------- ---------- ----------
  return (
    <div className='HomeCompo'>
        <div className='popularBookWrapper'>
            <Swiper 
            className="popularBookSwiper"
            modules={[Pagination, Autoplay]} 
            loop={true} 
            pagination={{
                clickable: true
            }}

            autoplay={{
                delay: 3000,
                disableOnInteraction: false, //내가 스와이프 해도 멈추지 마라
            }}    
            >
                {//  onMouseEnter={() => {swiperStopStart(true)}} onMouseLeave={() => {swiperStopStart(false)}}
                    popular_popularBook.map((obj, k)=>(
                        <SwiperSlide key={k}>
                            <div className='bg'>
                                <img src={obj.img} alt={obj.title}/>
                            </div>
                            <figure className='content'>
                                <p>
                                    <img src={obj.img} alt={obj.title}/>
                                </p>
                                <div className='right'>
                                    <figcaption>{titleFun(obj.title)}</figcaption> {/* '^'기호가 정규식 설정중 하나라서  단순히 '^'을 쓴다고 알리려면 \\를 같이 써줘야함('\' 두 번 쓴 이유는 \도 설정이 있어서 그냥 '\'쓴다는 뜻으로 두 번 쓴 거)*/}
                                    <span className='author'>작자: <code>{changeWord(obj.author, '\\^', ', ')}</code></span>
                                    <span className='publisher'>퍼블리셔: <code>{obj.publisher}</code></span>
                                    <button onClick={() => {moveTo(`/Detail/${obj.isbn}`)}}>자세히 보기</button>
                                </div>
                            </figure>
                        </SwiperSlide>
                    ))
                }

            </Swiper>
        </div>{/* div.popularBookWrapper */}

        <SearchCompo oriPla={false}/> {/* oriPla: original Place // 원래 검색장소 Search인지 구분하기 위함 // 아니라면 items을 찾는 동시에 Search로 이동 */}

        <article className='popularBlogReviewWrapper'>
            <h2>인기 블로그 리뷰</h2>
            <Swiper className='popularBlogReviewSwiper'
            slidesPerView={1.5}
            centeredSlides={true}
            spaceBetween={30}
            loop={true}
            breakpoints={{
                550: {
                  slidesPerView: 1.5,
                },
                600: {
                  slidesPerView: 2,
                }
              }} 
            >
                {
                    popular_popularBlogReview.map((obj, k) => (
                    <SwiperSlide key={k} onClick={() => {moveToUrl(obj.link)}}>
                        <div className='content'>
                            <strong>
                                {changeMultiWord(obj.title, ['<b>', '</b>'], ['', ''])}
                            </strong>
                            <b>{obj.bloggername}</b>
                            <p>
                            {changeMultiWord(obj.description, ['<b>', '</b>', '&gt;', '&lt;', '&quot;'], ['', '', '>', '<', '"'])}
                            </p>
                        </div>
                        <span className='itmeNumber'>{k+1}</span>
                    </SwiperSlide> 
                    ))
                }
                
            </Swiper>
        </article>

        <article className='popularYoutubeReviewWrapper'>
            <h2>인기 유튜브 리뷰</h2>
            <Swiper className='popularYoutubeReviewSwiper'
            slidesPerView={1.5}
            centeredSlides={true}
            spaceBetween={30}
            loop={true}
            breakpoints={{
                550: {
                  slidesPerView: 1.5,
                },
                600: {
                  slidesPerView: 2,
                }
              }} 
            >

                {
                    
                    popular_popularYoutubeReview.map((obj, k) => (
                        <SwiperSlide key={k} 
                        onClick={() => {moveToUrl(`https://www.youtube.com/watch?v=${obj.videoId}`)}} 
                        style={{'background': `URL(${obj.img}) center center / cover no-repeat scroll`}}
                        >
                            <div className='infoBg'>
                                <img className='playBtn' src='./icon_youtubePlay.svg' alt='playBtn'/>
                                <span className='durationTime'>{obj.durationTime}</span>
                            </div>
                            <span className='itmeNumber'>{k+1}</span>
                        </SwiperSlide>
                    ))
                }
                
            </Swiper>

            {/* <div className='YoutubeVideo' ref={elYoutubeVideo}>
                <button type='button' className='xBtn' onClick={inActiveYoutubeFun}></button>
                <iframe className='video'
                    src=''
                    title="video"
                >
                    해당 브라우저는 iframe을 지원하지 않습니다.
                </iframe>
            </div> */}
        </article>
    </div> //div.HomeCompo 
  )
}
